import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { SfMenuService } from '@safe-fleet/sf-menu';
import { IApplication } from '../../interfaces/IApplication';
import { EventTypeEnum, IMenuConfig, IMenuItem, ISecurityInfo, IUnauthorizedPopup } from '../../interfaces/IMenuConfig';
import { SecurityService } from '../security/security.service';
import { catchError, map, mergeMap } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class SfSecurityMenuService {
  infoMessageVisibilityChange: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(false);
  unauthorizedPopup: IUnauthorizedPopup;
  events$ = this.sfMenuService.events$;
  constructor(private readonly sfMenuService: SfMenuService, private readonly securityService: SecurityService) {}

  public configureMenu(config: IMenuConfig, securityInfo: ISecurityInfo): Observable<any> {
    this.securityService.setSecurityInformation(securityInfo.securityEndpoint, securityInfo.settingsEndpoint, securityInfo.headers);
    this.refreshMenu(config);
    this.onMenuChanges();
    this.infoMessageVisibilityChange.next(false);
    this.unauthorizedPopup = this.getUnauthorizedPopup(config.unauthorizedPopup);
    const permissionsObservable = this.securityService.getSecurityPermissions(config.currentProduct.id).pipe(
      catchError((error) => {
        if (error.status === 403 && config.multipleTenants && config.unauthorizedPopup) {
          this.infoMessageVisibilityChange.next(true);
        }
        throw error;
      })
    );

    const applicationsObservable = this.securityService.getApplications().pipe(
      map((applications: IApplication[]) => this.mapConfiguration(config, applications, securityInfo)),
      catchError((error) => {
        this.refreshMenu(config);
        throw error;
      })
    );

    const applicationInfoObservable = this.securityService.getApplicationInfo();
    return applicationsObservable.pipe(
      mergeMap(() => permissionsObservable),
      mergeMap(() => applicationInfoObservable),
      catchError(() => of(''))
    );
  }

  public refreshMenu(config: IMenuConfig) {
    this.sfMenuService.setMenuConfig(config);
  }
  public getPermissions() {
    return this.securityService.getPermissions();
  }

  public getSettingsInfo() {
    return this.securityService.getInfo();
  }

  private mapConfiguration(config: IMenuConfig, applications: IApplication[], securityInfo: ISecurityInfo) {
    config.products = [];
    const menuItems: IMenuItem[] = [];
    applications.forEach((app) => {
      menuItems.push(this.mapMenuApplication(app));
    });
    const selectedProduct = menuItems.find((app) => app.text === securityInfo.selectedApplication);
    if (!selectedProduct || selectedProduct === null) {
      config.products = menuItems;
      this.refreshMenu(config);
      return;
    }
    config.currentProduct = selectedProduct;
    config.products = menuItems.filter((item) => item.id !== config.currentProduct.id);
    this.refreshMenu(config);
  }

  private mapMenuApplication(app: IApplication) {
    return {
      text: app.applicationName,
      routeHref: app.applicationUrl,
      iconPath: app.applicationIconUrl,
      id: app.applicationName,
      newWindow: false,
      active: false,
      expanded: false,
      newTab: false,
    };
  }

  private getUnauthorizedPopup(popupConfig: IUnauthorizedPopup): IUnauthorizedPopup {
    return {
      popupButtonText: popupConfig ? popupConfig.popupButtonText : '',
      popupMessageActionText: popupConfig ? popupConfig.popupMessageActionText : '',
      popupMessageText: popupConfig ? popupConfig.popupMessageText : '',
    } as IUnauthorizedPopup;
  }

  public reloadPermissions(applicationName: string): Observable<any> {
    return this.securityService.getSecurityPermissions(applicationName);
  }

  private onMenuChanges() {
    return this.sfMenuService.events$.subscribe((menuEvent) => {
      if (menuEvent.type === EventTypeEnum.OpenTenantListClick) {
        this.infoMessageVisibilityChange.next(false);
      }
    });
  }
}
