import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { IApplication } from '../../interfaces/IApplication';

@Injectable({
  providedIn: 'root',
})
export class SecurityService {
  private securityEndpoint: string;
  private settingsEndpoint: string;
  private securityHeaders: any;
  private permissions: any;
  private applicationInfo: any;
  constructor(private readonly httpClient: HttpClient) {}

  public setSecurityInformation(securityEndpoint: string, settingsEndpoint: string, headers: any) {
    this.securityHeaders = headers;
    this.securityEndpoint = securityEndpoint;
    this.settingsEndpoint = settingsEndpoint;
  }

  public getApplications(): Observable<IApplication[]> {
    const url = `${this.securityEndpoint}/applications`;
    return this.httpClient.get<IApplication[]>(url, {
      headers: this.securityHeaders,
    });
  }

  public getPermissions() {
    return this.permissions;
  }

  public getInfo() {
    return this.applicationInfo;
  }

  public getSecurityPermissions(resourceName: string): Observable<any> {
    const url = `${this.securityEndpoint}/permissions?resourceName=${resourceName}`;
    return this.httpClient
      .get(url, {
        headers: this.securityHeaders,
      })
      .pipe(
        map((x) => (this.permissions = x)),
        catchError((error) => {
          throw error;
        })
      );
  }

  public getApplicationInfo(): Observable<any> {
    const url = `${this.settingsEndpoint}/info`;
    return this.httpClient
      .get(url, {
        headers: this.securityHeaders,
      })
      .pipe(
        map((x) => (this.applicationInfo = x)),
        catchError((error) => {
          throw error;
        })
      );
  }
}
